import { Component, OnInit } from "@angular/core";
import { routerTransition } from "../../router.animations";
import { NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { TransferStatusService } from "src/app/shared/services/transfer-status.service";
import { InfoComponent } from "./info/info.component";

@Component({
    selector: "app-transfer",
    templateUrl: "./transfer.component.html",
    styleUrls: ["./transfer.component.scss"],
    animations: [routerTransition()],
})
export class TransferComponent implements OnInit {
    transfers: any;
    constructor(
        private dialogService: NgbModal,
        private transferStatusService: TransferStatusService
    ) {}

    ngOnInit() {
        this.transferStatusService.get().subscribe(() => {});
        this.transferStatusService.getTransfersData().subscribe((data) => {
            this.transfers = data;
        });
    }

    info(transferId: any) {
        const modalRef = this.dialogService.open(InfoComponent, {
            size: "lg",
            centered: true,
        });
        modalRef.componentInstance.transferId = transferId;
    }
}
